import React from 'react';
import debounce from 'debounce'
import { Provider, themes, Flex, FlexItem, Dropdown, Input, Header, Label} from '@fluentui/react-northstar';
import { CloseIcon, AcceptIcon } from '@fluentui/react-icons-northstar';

const styles={
    mainPadding:{padding:"0px 2em"},
    mainSidePadding:{padding:"10px 2em"},
    mainPdf:{display:"flex", justifyContent:"center"},
    mainTable:{
        backgroundColor: "#fff",
        border: "1px solid #d8e2f0",
        borderRadius: "4px",
        boxShadow: "0 0 10px 0 rgba(19,29,41,.05)",
        position: "relative",
        padding: "0em 3em 2em"
    },
    backButton:{
        cusor:"pointer"
    },
    label:{
        fontWeight:"bold"
    }
}

const langs={
    en:{
        heading:"Moneybird Configuration",
        apiSection:{
            title: <label style={styles.label} >API Token</label>,
            description:  <span>This is the API Token for you MoneyBird Account. You can generate one <a target="_blank" href="https://moneybird.com/user/applications/new">here</a> for personal use.</span>,
            succsess: "API Token IS vaild",
            fail: "API Token NOT vaild",
            placeholder: 'API Token'
        },
        adminSection:{
            title: <label style={styles.label} >Administration</label>,
            description:<span>This is the administration you created your API Token for</span>,
            placeholder: 'Administration'
        },
        contactSection:{
            title:<label style={styles.label} >Contact</label>,
            description: <span>The contact whos invoice's will show up on the invoices tabs</span>,
            placeholder: 'Contact'
        },
        tabTitle:"Moneybird Invoices"
    },
    nl:{
        heading:"Moneybird Koppelen",
        apiSection:{
            title:<label style={styles.label} >API token invoeren</label>,
            description:  <span>Vul hier een API-token in van je Moneybird account. Je kunt <a target="_blank" href="https://moneybird.com/user/applications/new">hier</a> een API-token genereren. Vul een naam in die herkenbaar is voor deze connectie, bijvoorbeeld ‘Microsoft Teams Integratie’. Kies voor ‘API token voor persoonlijk gebruik’ en geef in ieder geval toegang voor alle onderdelen. Vervolgens verschijnt er een API-token in beeld. Vul deze hieronder in.</span>,
            succsess: "API-token niet geldig",
            fail: "API-token IS geldig",
            placeholder: 'API token invoeren'
        },
        adminSection:{
            title: <label style={styles.label} >Toediening</label>,
            description:<span>Dit is de administratie van waaruit u contacten wilt bekijken</span>,
            placeholder: "Administratie"
        },
        contactSection:{
            title:<label style={styles.label} >Contact</label>,
            description:  <span>Selecteer de Contact waarvan je de facturen wilt tonen in de Tab.</span>,
            placeholder:"contact"
        },
        tabTitle:"Moneybird facturen"
    }
}

class Config extends React.Component{
    constructor(){
        super();
        if(this.microsoftTeams)
            this.microsoftTeams.initialize();
        this.state ={
            apiKey:"",
            adminId:"",
            admins:null,
            admin:null,
            contacts:null,
            contact:null,
            keys:[],
            isNew:false,
            isOpen: false

        }
        this.handleChange = this.handleChange.bind(this)
        this.submit = this.submit.bind(this)
        this.getOptions = this.getOptions.bind(this)
        this.handleContact = this.handleContact.bind(this)
        this.save = this.save.bind(this)
        this.getAdmin = debounce(this.getAdmin,2000).bind(this)
        this.handleToken = this.handleToken.bind(this)
        this.handleAdmin = this.handleAdmin.bind(this)
    }

    async getAdmin(apiKey){
        try{
            const res = await fetch(
                `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                {
                    mode:"cors",
                    method:"POST",
                    body: JSON.stringify({
                        method:"GET",
                        path:`administrations.json`,
                        key: apiKey
                    })
                }
            )
            const json = await res.json()
            console.log({json})
            if(json.error)
                this.setState({admins:false})
            else
                this.setState({admins:json})
        }
        catch(e){
            console.log(e)
        }
    }

    componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
        this.microsoftTeams.initialize();
        this.microsoftTeams.getContext((async context=>{
            const {tid, locale} = context;
            try{
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/key/${tid}`,
                    {
                        mode:"cors",
                        method:"GET"
                    }
                )
                const json = await res.json()
                console.log({json})
                this.setState({keys:json.map(k=>k.key), locale})
    
            }
            catch(e){
                console.log("BOOMER!")
                console.log(e)
            }
    }).bind(this))
    }

    getOptions(){
        const {contacts} = this.state;
        return contacts.map(c=>({content:c.company_name, value:c.id}))
    }

    handleContact(e, value){
        this.setState({ contact: value.value }, this.save)
    }

    handleAdmin(e, value){
        this.setState({ admin: value.value })
        this.submit(value.value)
    }

  
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }
    handleToken(e, v){
        console.log({e, v})
        if(e){
            this.setState({apiKey:e.target.value, isOpen: true})
            this.getAdmin(e.target.value);
        }
        else if(v){
            this.setState({apiKey:v.value, isOpen:false})
            this.getAdmin(v.value );
        }

    }
    async submit({value :adminId}){
        console.log(adminId)
        const {apiKey, isNew} = this.state;

        this.microsoftTeams.initialize();
        this.microsoftTeams.getContext(async context=>{
            const {tid} = context;
            try{
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/contacts.json`,
                            key: apiKey,
                            tid,
                            isNew
                        })
                    }
                )
                const json = await res.json()
                console.log({json})
                this.setState({contacts:json})
    
            }
            catch(e){
                console.log("BOOM!")
                console.log(e)
            }
        })
    }

    save(){
        
        const {apiKey, admin:{value: adminId}, contact, locale} = this.state;
        const lang = locale.split("-")[0];

        this.microsoftTeams.initialize();
        this.microsoftTeams.settings.setValidityState(true);
        this.microsoftTeams.settings.registerOnSaveHandler((saveEvent) => {
            this.microsoftTeams.settings.setSettings({
                websiteUrl: `https://jp5525.github.io/teams-demo/`,
                contentUrl: `https://jp5525.github.io/teams-demo/`,
                entityId: JSON.stringify({apiKey, adminId, contact}),
                suggestedDisplayName: langs[lang].tabTitle
            });
            saveEvent.notifySuccess();
        });
    }

    render(){
        const {contacts, contact, admins, admin, apiKey, keys, isOpen, locale } = this.state;
        const lang = locale? langs[locale.split("-")[0]]: null
        console.log({keys})
        return (
            lang?
                <Provider theme={themes.teams}>
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <Header content="MoneyBird Opstelling" />
                            <Flex gap="gap.small" vAlign="center" hAlign="start">
                                <FlexItem justifyContent="center">
                                    <form onSubmit={this.submit} style={{width:"100%"}}>
                                        
                                        {lang.apiSection.title} <br/>
                                        {lang.apiSection.description}
                                        {admins== false? <> <br/><Label content={lang.apiSection.fail} icon={{as: CloseIcon}}/></> : null}
                                        {admins? <> <br/><Label content={lang.apiSection.succsess} icon={{as: AcceptIcon}}/></> : null}
                                        <Dropdown
                                            items={keys}
                                            placeholder={lang.apiSection.placeholder}
                                            onChange={this.handleToken}
                                            name="apiKey"
                                            id="apiKey"
                                            clearable={apiKey && apiKey.length>0}
                                            fluid={true}
                                            search={true}
                                            open={isOpen}
                                            noResultsMessage={{
                                                content:`Add... ${apiKey}`, 
                                                onClick: m=>{
                                                    this.setState({isNew:true, keys:[apiKey, ...keys], isOpen:false}, this.getAdmin(apiKey))
                                                    
                                                }, 
                                                style:{cursor: "pointer"}
                                            }}
                                        />
                                        
                                        <br />

                                        {lang.adminSection.title} <br/>
                                        {lang.adminSection.description}
                                        <Dropdown
                                            items={admins? admins.map(a=>({content:a.name, value:a.id})): []}
                                            value={admin? admin.content : null}
                                            placeholder={lang.adminSection.placeholder}
                                            onChange={this.handleAdmin}
                                            name="Admin"
                                            disabled={admins? false: true}
                                            fluid={true}
                                            />

                                        <br />

                                        {lang.contactSection.title} <br/>
                                        {lang.contactSection.description}
                                        <Dropdown
                                            items={contacts? this.getOptions(): []}
                                            value={contact? contact.content : null}
                                            placeholder={lang.contactSection.placeholder}
                                            onChange={this.handleContact}
                                            name="contact"
                                            disabled={contacts? false: true}
                                            />
                
                                    </form>
                                </FlexItem>
                            </Flex>
                        </div>
                    </div>
                </Provider>:
                null
            
            
        )

    }
}

export default Config;
